    /* Color Palette - Clean & Chill */
    --color-bg-primary: hsl(0, 8%, 90%); /* Stone 50 */
    --color-bg-secondary: #FFFFFF; /* White */
    --color-bg-secondary-hover: #FAFAF9; /* Stone 50 */
    
    --color-text-primary: #1F2937; /* Gray 800 */
    --color-text-secondary: #6B7280; /* Gray 500 */
    --color-text-inverted: #FFFFFF;

    /* Accents */
    /* Green - Legacy Brand Accent */
    --color-accent-green: #619e83;
    --color-accent-green-hover: #4e7e69;
    --color-accent-green-light: #eff7f4;

    /* Red - Legacy Brand Secondary */
    --color-accent-red: #9e4145;
    --color-accent-red-hover: #7e3437;
    --color-accent-red-light: #fcf2f2;

    /* UI Elements */
    --color-border: #E5E7EB; /* Gray 200 */
    --shadow-soft: 0 4px 6px -1px rgba(0, 0, 0, 0.05), 0 2px 4px -1px rgba(0, 0, 0, 0.03);
    --shadow-hover: 0 10px 15px -3px rgba(0, 0, 0, 0.05), 0 4px 6px -2px rgba(0, 0, 0, 0.03);
    
    --radius-sm: 0.375rem;
    --radius-md: 0.5rem;
    --radius-lg: 1rem;
    --radius-xl: 1.5rem;

    /* Dark Theme Palette (for Result Area) */
    --color-bg-dark-panel: #1F2937; /* Gray 800 */
    --color-bg-dark-hover: #374151; /* Gray 700 */
    --color-text-dark-primary: #F9FAFB; /* Gray 50 */
    --color-text-dark-secondary: #9CA3AF; /* Gray 400 */
    --color-border-dark: #4B5563; /* Gray 600 */
    --color-text-menu-fixed: #374151; /* Gray 700 - Fixed dark text for white menus */
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

html {
    background: var(--color-accent-green);
    &::after {
        content: '';
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100vw;
        height: 50vh;
        background-color: var(--color-bg-primary);
        z-index: -1;
    }
}

body {
    font-family: 'Noto Sans JP', sans-serif;
    color: var(--color-text-primary);
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 700;
    color: var(--color-text-primary);
}

a {
    text-decoration: none;
    color: inherit;
    transition: opacity 0.2s ease;
}

button {
    cursor: pointer;
    font-family: 'Noto Sans JP', sans-serif;
}

/* Utilities */
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
