___CSS_LOADER_EXPORT___.push([module.id, `.result-container-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 1.5rem;
    border-radius: var(--radius-lg); /* Match input radius */
    transition: background-color 0.2s ease, color 0.2s ease;
}

/* Dark Theme applied to the container */
.result-container-inner.dark-result {
    background-color: var(--color-bg-dark-panel);
    color: var(--color-text-dark-primary);
}

.result-actions {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
    margin-top: auto; /* Push to bottom */
    padding-top: 1rem; /* Some breathing room from content */
}

.export-group {
    display: flex;
    gap: 0.5rem;
    padding-left: 0.5rem;
    margin-left: 0.5rem;
    border-left: 1px solid var(--color-border);
    align-items: center;
}

.action-button {
    background-color: transparent;
    border: none;
    color: var(--color-text-secondary);
    padding: 0.5rem 0.75rem;
    border-radius: var(--radius-md);
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-weight: 500;
    font-size: 0.9rem;
}

.action-button:hover {
    background-color: var(--color-bg-secondary-hover);
    color: var(--color-text-primary);
    border-color: var(--color-text-secondary);
}

.result-container-inner.dark-result .action-button {
    color: var(--color-text-dark-secondary); /* Light gray for dark mode */
}

.result-container-inner.dark-result .action-button:hover {
    background-color: var(--color-bg-dark-hover);
    color: var(--color-text-dark-primary);
}

.theme-toggle.active {
    color: var(--color-accent-green);
    background-color: var(--color-accent-green-light);
}

.result-content {
    flex: 1;
    position: relative;
    padding: 0;
}

.empty-state {
    display: flex;
    justify-content: flex-start; /* Align to top-left like textarea */
    align-items: flex-start;
    height: 100%;
    color: var(--color-text-secondary);
    font-size: 1.5rem; /* Match input size */
    opacity: 0.6; /* Match input placeholder opacity */
    padding: 0; 
}

.empty-state p {
    font-size: 1.5rem;
    margin: 0;
    color: inherit;
    line-height: 1.6;
}

.result-area {
    font-size: 1.5rem;
    line-height: 2;
    color: var(--color-text-primary);
    padding: 0; /* Remove padding to align with Input */
    border-radius: var(--radius-md);
    transition: background-color 0.2s ease, color 0.2s ease;
}

/* Ruby Styling */
ruby {
    /* Standard ruby display */
    display: inline-block; /* Fallback */
    display: ruby;
}

rt {
    font-size: 0.6em;
    line-height: 1.2;
    user-select: none;
    text-align: center;
    color: var(--color-text-secondary); /* Milder color */
}

.dark-theme .result-area {
     background-color: var(--color-bg-dark-panel);
     color: var(--color-text-dark-primary);
}

.result-container-inner.tone-down {
    opacity: 0.7;
    filter: grayscale(0.5);
    pointer-events: none; 
    user-select: none;

}

.toast-notification {
    position: absolute;
    bottom: 100%;
    left: 0;
    margin-bottom: 0.5rem;
    background-color: var(--color-accent-green);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: var(--radius-md);
    font-size: 0.875rem;
    white-space: nowrap;
    box-shadow: var(--shadow-hover);
    animation: fadeInOut 2s ease forwards;
    z-index: 10;
    pointer-events: none; /* Let clicks pass through if it ever covers something, though outside flow */
}

@keyframes fadeInOut {
    0% { opacity: 0; transform: translateY(-10px); }
    10% { opacity: 1; transform: translateY(0); }
    90% { opacity: 1; transform: translateY(0); }
    100% { opacity: 0; transform: translateY(-10px); }
}

.action-group-left {
    display: flex;
    gap: 0.5rem;
    position: relative;
}

.save-menu-container {
    position: relative;
    display: inline-block;
}

.save-menu-dropdown {
    position: absolute;
    bottom: 100%;
    right: 0;
    width: 100px;
    background-color: var(--color-bg-secondary); /* Enforce white background */
    border: 1px solid var(--color-border);
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-lg);
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    z-index: 20;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.25rem;
    /* Force dark text color since background is white */
    color: var(--color-text-menu-fixed); 
    text-align: left;
}

/* Ensure background is solid */

/* Switch Styling */
.theme-switch-container {
    display: flex;
    justify-content: center;    
    align-items: center;
    width: 100%;
    padding: 0.5rem 0.75rem;
    gap: 0.5rem;
    cursor: default;
    color: var(--color-text-secondary); /* Force dark text */
    border-bottom: 1px solid var(--color-border);
}

.switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 20px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 32px;
    width: 32px;
}

/* TODO: revisit to clean up css, use variables to maintain slider */

.slider:before {
    position: absolute;
    content: "";
    height: 14px;
    width: 14px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: var(--color-accent-green);
}

input:focus + .slider {
    box-shadow: 0 0 1px var(--color-accent-green);
}

input:checked + .slider:before {
    transform: translateX(12px);
}

.menu-item {
    background-color: transparent;
    border: none;
    color: var(--color-text-secondary);
    padding: 0.5rem 0.75rem;
    border-radius: var(--radius-md);
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-weight: 500;
    font-size: 0.9rem;
}

.menu-item:hover {
    background-color: var(--color-bg-secondary-hover);
    color: var(--color-text-primary);
    border-color: var(--color-text-secondary);
}

/* Ensure empty state text handles dark mode */
.result-container-inner.dark-result .empty-state {
    color: var(--color-text-dark-secondary);
}

.result-container-inner.dark-result rt {
    color: var(--color-text-dark-secondary);
}

/* Force all standard text in dark result to be light */
.result-container-inner.dark-result .result-area,
.result-container-inner.dark-result .result-area span,
.result-container-inner.dark-result .result-area ruby {
    color: var(--color-text-dark-primary);
}
