___CSS_LOADER_EXPORT___.push([module.id, `.input-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
    flex: 1; /* Take up available space in panel */
}

.input-description {
    font-size: 1rem;
    color: var(--color-text-secondary);
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.input-area {
    width: 100%;
    padding: 1.5rem;
    background-color: transparent;
    border: none;
    border-radius: var(--radius-lg);
    font-size: 1.5rem;
    line-height: 2; /* Match Result panel for alignment */
    color: var(--color-text-primary);
    outline: none;
    transition: all 0.2s ease;
    resize: none; 
    font-family: inherit; /* Ensure font matches */
}

.input-area::placeholder {
    color: var(--color-text-secondary);
    opacity: 0.6;
}

.input-actions {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    display: flex;
    gap: 0.75rem;
    align-items: center;
    z-index: 10;
}

.generate-button {
    width: 2.5rem;
    height: 2.5rem;
    background-color: var(--color-bg-secondary);
    border-radius: 50%;
    color: var(--color-text-secondary);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    cursor: pointer;
}

.generate-button:hover {
    background-color: var(--color-bg-secondary-hover);
    color: var(--color-text-primary);
    transform: rotate(15deg);
}

/* Paste Button - Mobile Only */
.paste-button {
    display: none; /* Hidden by default (desktop) */
}

@media (max-width: 768px) {
    .paste-button {
        /* Styles matched to generate-button */
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2.5rem;
        height: 2.5rem;
        border-radius: 50%;
        background-color: var(--color-bg-secondary);
        color: var(--color-text-secondary);
        transition: all 0.2s ease;
        cursor: pointer;
    }

    .paste-button:active {
        background-color: var(--color-bg-secondary-hover);
        transform: scale(0.95);
    }
}`, "",{"version":3,"sources":["webpack://./src/components/Input.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;IAClB,OAAO,EAAE,qCAAqC;AAClD;;AAEA;IACI,eAAe;IACf,kCAAkC;IAClC,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,eAAe;IACf,6BAA6B;IAC7B,YAAY;IACZ,+BAA+B;IAC/B,iBAAiB;IACjB,cAAc,EAAE,qCAAqC;IACrD,gCAAgC;IAChC,aAAa;IACb,yBAAyB;IACzB,YAAY;IACZ,oBAAoB,EAAE,wBAAwB;AAClD;;AAEA;IACI,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,aAAa;IACb,YAAY;IACZ,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,cAAc;IACd,2CAA2C;IAC3C,kBAAkB;IAClB,kCAAkC;IAClC,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,iDAAiD;IACjD,gCAAgC;IAChC,wBAAwB;AAC5B;;AAEA,+BAA+B;AAC/B;IACI,aAAa,EAAE,gCAAgC;AACnD;;AAEA;IACI;QACI,sCAAsC;QACtC,aAAa;QACb,mBAAmB;QACnB,uBAAuB;QACvB,aAAa;QACb,cAAc;QACd,kBAAkB;QAClB,2CAA2C;QAC3C,kCAAkC;QAClC,yBAAyB;QACzB,eAAe;IACnB;;IAEA;QACI,iDAAiD;QACjD,sBAAsB;IAC1B;AACJ","sourcesContent":[".input-section {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n    position: relative;\n    flex: 1; /* Take up available space in panel */\n}\n\n.input-description {\n    font-size: 1rem;\n    color: var(--color-text-secondary);\n    font-weight: 500;\n    margin-bottom: 0.5rem;\n}\n\n.input-area {\n    width: 100%;\n    padding: 1.5rem;\n    background-color: transparent;\n    border: none;\n    border-radius: var(--radius-lg);\n    font-size: 1.5rem;\n    line-height: 2; /* Match Result panel for alignment */\n    color: var(--color-text-primary);\n    outline: none;\n    transition: all 0.2s ease;\n    resize: none; \n    font-family: inherit; /* Ensure font matches */\n}\n\n.input-area::placeholder {\n    color: var(--color-text-secondary);\n    opacity: 0.6;\n}\n\n.input-actions {\n    position: absolute;\n    bottom: 1rem;\n    right: 1rem;\n    display: flex;\n    gap: 0.75rem;\n    align-items: center;\n    z-index: 10;\n}\n\n.generate-button {\n    width: 2.5rem